<template>
  <div :class="{'has-logo':showLogo}">
    <div class="nav-logo">
      <logo v-if="showLogo" />
    </div>

    <el-menu
      :default-active="activeIndex"
      :background-color="variables.navMenuBg"
      :text-color="variables.navMenuText"
      :active-text-color="variables.navMenuActiveText"
      mode="horizontal"
      @select="handleSelect"
    >
      <el-menu-item index="nav-home">首页</el-menu-item>
      <el-menu-item index="nav-pms">商品管理</el-menu-item>
      <el-submenu index="nav-crm">
        <template slot="title">客户管理</template>
        <el-menu-item index="2-1">选项1</el-menu-item>
        <el-submenu index="2-4">
          <template slot="title">选项4</template>
          <el-menu-item index="2-4-1">选项1</el-menu-item>
        </el-submenu>
      </el-submenu>
      <el-menu-item index="nav-settings">企业管理</el-menu-item>
      <el-menu-item index="nav-help" disabled>帮助</el-menu-item>
    </el-menu>

    <el-dropdown @command="handleCommand">
      <span class="el-dropdown-link">
        <img :src="avatar+'?imageView2/1/w/80/h/80'" class="avatar">
      </span>
      <el-dropdown-menu slot="dropdown">
        <el-dropdown-item command="security">安全管理</el-dropdown-item>
        <el-dropdown-item command="profile">基本资料</el-dropdown-item>
        <el-dropdown-item command="authorize">实体认证</el-dropdown-item>
        <el-dropdown-item command="contacts">联系人管理</el-dropdown-item>
        <el-dropdown-item divided command="logout">退出登录</el-dropdown-item>
      </el-dropdown-menu>
    </el-dropdown>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import Logo from './Logo'
import variables from '@/styles/variables.scss'

export default {
  components: { Logo },
  data() {
    return {
      // avatar: require('@/assets/img/favicon.png'),
      activeIndex: 'nav-home'
    }
  },
  computed: {
    ...mapGetters({
      sidebar: 'sidebar',
      userName: 'user/userName',
      avatar: 'user/avatar'
    }),
    showLogo() {
      return this.$store.state.settings.navmenuLogo
    },
    variables() {
      return variables
    }
  },
  methods: {
    async handleSelect(index, indexPath) {
      await this.$store.dispatch('routes/toggleSidebarRoutes', indexPath[0])
      switch (index) {
        case 'nav-home':this.$router.push('/'); break
        case 'nav-pms':this.$router.push('/goods/config'); break
        case 'nav-crm':this.$router.push('/crm'); break
        case 'nav-settings':this.$router.push('/settings/profile'); break
        case 'nav-help':this.$router.push('/'); break
      }
    },
    async handleCommand(command) {
      // this.$message('click on item ' + command);
      switch (command) {
        case 'security':this.$router.push('/settings/safety'); break
        case 'profile':this.$router.push('/settings/profile'); break
        case 'authorize':this.$router.push('/settings/authorize'); break
        case 'contacts':this.$router.push('/settings/contacts'); break
        case 'logout':
          await this.$store.dispatch('logout')
          // this.$router.push('/login?redirect=${this.$route.fullPath}')
          // this.$router.push('/homepage')
          break
        default:
          return
      }
      this.activeIndex = 'nav-settings'
      await this.$store.dispatch('routes/toggleSidebarRoutes', 'nav-settings')
    }
  }
}
</script>

