import request from '@/utils/request'

export function getInfo(uname) {
  return request({
    url: '/users',
    method: 'get',
    params: {
      uname: uname
    }
  })
}

export function login(data) {
  return request({
    url: '/login',
    method: 'post',
    data: data
  })
}

export function logout() {
  return request({
    url: '/logout',
    method: 'post'
  })
}
