import router from './router'
import store from './store'
import NProgress from 'nprogress' // progress bar
import 'nprogress/nprogress.css' // progress bar style
import getPageTitle from '@/utils/get-page-title'

NProgress.configure({ showSpinner: false }) // NProgress Configuration

const whiteList = ['/homepage', '/login'] // no redirect whitelist

router.beforeEach(async(to, from, next) => {
  NProgress.start()
  document.title = getPageTitle(to.meta.title)

  const aud = store.getters['audience']
  const auth = store.getters['authorization']
  if (aud !== '' && auth !== '') {
    if (to.path === '/login') {
      next({ path: '/' })
    } else {
      var appInit = store.getters['appInit']
      if (appInit) {
        next()
      } else {
        var ugroup = parseInt(store.getters['user/ugroup'], 10)
        if (isNaN(ugroup) || ugroup < 0) {
          await store.dispatch('user/getInfo')
          ugroup = parseInt(store.getters['user/ugroup'], 10)
        }
        if (ugroup && ugroup >= 0) {
          // dynamically add accessible routes
          const accessRoutes = await store.dispatch('routes/generateRoutes', ugroup)
          router.addRoutes(accessRoutes)
          store.dispatch('user/clearCache')
          store.dispatch('app/toggleInit', true)
          store.dispatch('routes/toggleSidebarRoutes', 'nav-home')
          // hack method to ensure that addRoutes is complete
          // set the replace: true, so the navigation will not leave a history record
          // store.commit('app/SET_INIT', true)
          next({ ...to, replace: true })
        } else {
          await store.dispatch('logout')
          // next(`/login?redirect=${to.path}`)
        }
      }
    }
    NProgress.done()
  } else {
    // has no auth info
    if (whiteList.indexOf(to.path) !== -1) {
      next()
    } else {
      await store.dispatch('logout')
      // router.push('/login')
      // next(`/login?redirect=${to.path}`)
      // router.push('/homepage')
      NProgress.done()
    }
  }
})

router.afterEach((to, from) => {
  NProgress.done()
})
