import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

/* Layout */
import Layout from '@/layout'

/**
 * Note: sub-menu only appear when route children.length >= 1
 *
 * hidden: true                   if set true, item will not show in the sidebar(default is false)
 * alwaysShow: true               if set true, will always show the root menu
 *                                if not set alwaysShow, when item has more than one children route,
 *                                it will becomes nested mode, otherwise not show the root menu
 * redirect: noRedirect           if set noRedirect will no redirect in the breadcrumb
 * name:'router-name'             the name is used by <keep-alive> (must set!!!)
 * meta : {
    roles: ['admin','editor']    control the page roles (you can set multiple roles)
    title: 'title'               the name show in sidebar and breadcrumb (recommend set)
    icon: 'svg-name'/'el-icon-x' the icon show in the sidebar
    breadcrumb: false            if set false, the item will hidden in breadcrumb(default is true)
    activeMenu: '/example/list'  if set path, the sidebar will highlight the path you set
  }
 */

/**
 * constantRoutes
 * a base page that does not have permission requirements
 * all roles can be accessed
 */
export const constantRoutes = [
  {
    path: '/homepage',
    name: 'Home',
    beforeEnter() { location.href = 'https://www.runqiaoinfo.com' }
  },
  {
    path: '/login',
    name: 'Login',
    component: () => import('@/views/login')
  },
  {
    path: '/logout',
    name: 'Logout',
    redirect: '/login'
  },
  {
    path: '/404',
    name: '404',
    component: () => import('@/views/404')
  },
  {
    path: '/',
    component: Layout,
    redirect: '/dashboard',
    meta: { navMenuIndex: 'nav-home' },
    children: [
      {
        path: 'dashboard',
        name: 'Dashboard',
        component: () => import('@/views/dashboard/index'),
        meta: { title: '概况', icon: 'dashboard', navMenuIndex: 'nav-home' }
      }
    ]
  },
  {
    path: '/settings',
    component: Layout,
    name: 'Settings',
    meta: { title: '账号管理', icon: 'nested', navMenuIndex: 'nav-settings' },
    children: [
      {
        path: 'profile',
        name: 'Profile',
        component: () => import('@/views/settings/profile'),
        meta: { title: '基本资料', icon: 'table', navMenuIndex: 'nav-settings' }
      },
      {
        path: 'authorize',
        name: 'Authorize',
        component: () => import('@/views/settings/authorize'),
        meta: { title: '实体认证', icon: 'table', navMenuIndex: 'nav-settings' }
      },
      {
        path: 'contacts',
        name: 'Contacts',
        component: () => import('@/views/settings/contacts'),
        meta: { title: '联系人管理', icon: 'table', navMenuIndex: 'nav-settings' }
      },
      {
        path: 'safety',
        name: 'Safety',
        component: () => import('@/views/settings/safety'),
        meta: { title: '安全管理', icon: 'table', navMenuIndex: 'nav-settings' }
      }
    ]
  }
]
/**
 * asyncRoutes
 * the routes that need to be dynamically loaded based on user roles
 */
export const asyncRoutes = [
  {
    path: '/goods',
    component: Layout,
    name: 'Goods',
    meta: {
      title: '商品管理',
      icon: 'product',
      ugroup: 1000,
      navMenuIndex: 'nav-pms'
    },
    children: [
      {
        path: 'config',
        name: 'GoodsConfig',
        component: () => import('@/views/goods/index'),
        meta: { title: '商品配置', icon: 'table', navMenuIndex: 'nav-pms' }
      },
      {
        path: 'contract',
        name: 'GoodsContract',
        component: () => import('@/views/goods/contract/index'),
        meta: { title: '合同管理', icon: 'table', navMenuIndex: 'nav-pms' }
      }
    ]
  },
  {
    path: '/crm',
    component: Layout,
    redirect: '/list',
    meta: {
      title: '客户管理',
      icon: 'product',
      ugroup: 1000,
      navMenuIndex: 'nav-crm'
    },
    children: [
      {
        path: 'list',
        name: 'List',
        component: () => import('@/views/crm/index'),
        meta: { title: '客户列表', icon: 'product', navMenuIndex: 'nav-crm' }
      }
    ]
  },
  {
    path: '/nested',
    component: Layout,
    redirect: '/nested/menu1',
    name: 'Nested',
    meta: {
      title: 'Nested',
      icon: 'nested',
      ugroup: 1,
      navMenuIndex: 'nav-help'
    },
    children: [
      {
        path: 'menu1',
        component: () => import('@/views/examples/nested/menu1/index'), // Parent router-view
        name: 'Menu1',
        meta: { title: 'Menu1' },
        children: [
          {
            path: 'menu1-1',
            component: () => import('@/views/examples/nested/menu1/menu1-1'),
            name: 'Menu1-1',
            meta: { title: 'Menu1-1' }
          },
          {
            path: 'menu1-2',
            component: () => import('@/views/examples/nested/menu1/menu1-2'),
            name: 'Menu1-2',
            meta: { title: 'Menu1-2' },
            children: [
              {
                path: 'menu1-2-1',
                component: () => import('@/views/examples/nested/menu1/menu1-2/menu1-2-1'),
                name: 'Menu1-2-1',
                meta: { title: 'Menu1-2-1' }
              },
              {
                path: 'menu1-2-2',
                component: () => import('@/views/examples/nested/menu1/menu1-2/menu1-2-2'),
                name: 'Menu1-2-2',
                meta: { title: 'Menu1-2-2' }
              }
            ]
          },
          {
            path: 'menu1-3',
            component: () => import('@/views/examples/nested/menu1/menu1-3'),
            name: 'Menu1-3',
            meta: { title: 'Menu1-3' }
          }
        ]
      },
      {
        path: 'menu2',
        component: () => import('@/views/examples/nested/menu2/index'),
        meta: { title: 'menu2' }
      }
    ]
  },
  // 404 page must be placed at the end !!!
  { path: '*', redirect: '/404', hidden: true }
]

const createRouter = () => new Router({
  // mode: 'history', // require service support
  scrollBehavior: () => ({ y: 0 }),
  routes: constantRoutes
})

const router = createRouter()

// Detail see: https://github.com/vuejs/vue-router/issues/1234#issuecomment-357941465
export function resetRouter() {
  const newRouter = createRouter()
  router.matcher = newRouter.matcher // reset router
}

export default router
