import { asyncRoutes, constantRoutes } from '@/router'

/**
 * Use meta.role to determine if the current user has permission
 * @param ugroup
 * @param route
 */
function hasPermission(ugroup, route) {
  if (route.meta && route.meta.ugroup) {
    // return ugroup.some(ugroup => route.meta.ugroup.includes(ugroup))
    return route.meta.ugroup === ugroup
  } else {
    return true
  }
}

/**
 * Filter asynchronous routing tables by recursion
 * @param routes asyncRoutes
 * @param ugroup
 */
function filterAsyncRoutes(routes, ugroup) {
  const res = []
  routes.forEach(route => {
    const tmp = { ...route }
    if (hasPermission(ugroup, tmp)) {
      if (tmp.children) {
        tmp.children = filterAsyncRoutes(tmp.children, ugroup)
      }
      res.push(tmp)
    }
  })
  return res
}

function inCurNavMenu(navMenuIndex, route) {
  if (route.meta && route.meta.navMenuIndex) {
    // return ugroup.some(ugroup => route.meta.ugroup.includes(ugroup))
    return route.meta.navMenuIndex === navMenuIndex
  } else {
    return false
  }
}

function filterSidebarRoutes(routes, navMenuIndex) {
  const res = []
  routes.forEach(route => {
    const tmp = { ...route }
    if (inCurNavMenu(navMenuIndex, tmp)) {
      if (tmp.children) {
        tmp.children = filterSidebarRoutes(tmp.children, navMenuIndex)
      }
      res.push(tmp)
    }
  })
  return res
}

const state = {
  routes: [],
  addRoutes: [],
  sidebarRoutes: []
}

const mutations = {
  SET_ROUTES: (state, routes) => {
    state.addRoutes = routes
    state.routes = constantRoutes.concat(routes)
  },
  SET_SIDEBAR_ROUTES: (state, routes) => {
    state.sidebarRoutes = routes
  }
}

const actions = {
  generateRoutes({ commit }, ugroup) {
    return new Promise(resolve => {
      let accessedRoutes
      if (ugroup === 36) {
        accessedRoutes = asyncRoutes || []
      } else {
        accessedRoutes = filterAsyncRoutes(asyncRoutes, ugroup)
      }
      commit('SET_ROUTES', accessedRoutes)
      resolve(accessedRoutes)
    })
  },

  toggleSidebarRoutes({ state, commit }, navMenuIndex) {
    return new Promise(resolve => {
      const sidebarRoutes = filterSidebarRoutes(state.routes, navMenuIndex)
      commit('SET_SIDEBAR_ROUTES', sidebarRoutes)
      resolve(sidebarRoutes)
    })
  }
}

export default {
  namespaced: true,
  state,
  mutations,
  actions
}
