import Cookies from 'js-cookie'

const AudKey = 'aud'
const AuthKey = 'auth'
const UGroupKey = 'ugroup'
const USCCKey = 'uscc'
const AvatarKey = 'avatar'

// Audience
export function getAud() {
  var aud = Cookies.get(AudKey)
  return aud == null || aud === undefined ? '' : aud
}

export function setAud(aud) {
  return Cookies.set(AudKey, aud)
}

export function removeAud() {
  return Cookies.remove(AudKey)
}

// AuthToken
export function getAuthToken() {
  var auth = Cookies.get(AuthKey)
  return auth == null || auth === undefined ? '' : auth
}

export function setAuthToken(token) {
  return Cookies.set(AuthKey, token)
}

export function removeAuthToken() {
  return Cookies.remove(AuthKey)
}

// UGroup
export function getUGroup() {
  return Cookies.get(UGroupKey)
}

export function setUGroup(ugroup) {
  return Cookies.set(UGroupKey, ugroup)
}

export function removeUGroup() {
  return Cookies.remove(UGroupKey)
}

// USCC
export function getUSCC() {
  return Cookies.get(USCCKey)
}

export function setUSCC(uscc) {
  return Cookies.set(USCCKey, uscc)
}

export function removeUSCC() {
  return Cookies.remove(USCCKey)
}

// Avatar
export function getAvatar() {
  return Cookies.get(AvatarKey)
}

export function setAvatar(avatar) {
  return Cookies.set(AvatarKey, avatar)
}

export function removeAvatar() {
  return Cookies.remove(AvatarKey)
}

