import { login, logout, getInfo } from '@/api/user'
import { getAud, setAud, removeAud, getAuthToken, setAuthToken, removeAuthToken,
  getUGroup, setUGroup, removeUGroup, getUSCC, setUSCC, removeUSCC,
  getAvatar, setAvatar, removeAvatar } from '@/utils/cache'
import router, { resetRouter } from '@/router'

const state = {
  aud: getAud(),
  auth: getAuthToken(),
  ugroup: getUGroup(),
  uname: getAud(),
  avatar: getAvatar(),
  uid: '',
  mobile: '',
  email: '',
  cid: '',
  uscc: getUSCC()
}

const getters = {
  audience: state => state.aud,
  authorization: state => state.auth,
  ugroup: state => state.ugroup,
  userId: state => state.uid,
  userName: state => state.uname,
  avatar: state => state.avatar,
  mobilePhone: state => state.mobile,
  email: state => state.email,
  cid: state => state.cid,
  uscc: state => state.uscc,
  info: (state, getters, rootState, rootGetters) => {
    return state
  }
}

const mutations = {
  SET_AUD: (state, aud) => { state.aud = aud },
  SET_AUTH: (state, auth) => { state.auth = auth },
  SET_UNAME: (state, uname) => { state.uname = uname },
  SET_UGROUP: (state, ugroup) => { state.ugroup = ugroup },
  SET_USCC: (state, uscc) => { state.uscc = uscc },
  SET_AVATAR: (state, avatar) => { state.avatar = avatar },
  SET_INFO: (state, payload) => {
    state.uid = payload.uid
    state.uname = payload.uname
    state.ugroup = payload.ugroup
    state.mobile = payload.mobile
    state.email = payload.email
    state.cid = payload.cid
    state.uscc = payload.uscc
    state.avatar = payload.avatar
    // setUGroup(payload.ugroup)
    // setUSCC(payload.uscc)
    // setAvatar(payload.avatar)
  }
}

const actions = {
  getInfo({ state, commit, rootState }) {
    return new Promise((resolve, reject) => {
      getInfo(state.uname).then(response => {
        const { status, data } = response.data
        if (!data || data.lenght === 0) {
          reject('Verification failed, please Login again.')
        }
        commit('SET_INFO', data[0])
        resolve(data[0])
      }).catch(error => {
        reject(error)
      })
    })
  },

  login: {
    root: true,
    handler({ commit }, payload) {
      const { username, password } = payload
      return new Promise((resolve, reject) => {
        login({ username: username.trim(), password: password }).then(response => {
          const { status, data } = response.data
          if (status === 200 && data != null) {
            commit('SET_UNAME', response.headers['audience'])
            commit('SET_UGROUP', data.ugroup)
            commit('SET_USCC', data.uscc)
            commit('SET_AVATAR', data.avatar)
            // setUGroup(data.ugroup)
            // setUSCC(data.uscc)
            // setAvatar(data.avatar)
          } else {
            reject('Verification failed, please Login again.')
          }
          commit('SET_AUD', response.headers['audience'])
          // setAud(response.headers['audience'])
          commit('SET_AUTH', response.headers['authorization'])
          // setAuthToken(response.headers['authorization'])
          resolve()
        }).catch(error => {
          reject(error)
        })
      })
    }
  },

  logout: {
    root: true,
    handler({ dispatch, commit, state }) {
      return new Promise((resolve, reject) => {
        logout().then(() => {
          resolve()
        }).catch(error => {
          reject(error)
        })
        dispatch('app/toggleInit', false, { root: true })
        commit('SET_AUD', '')
        commit('SET_AUTH', '')
        commit('SET_UGROUP', '')
        removeAud()
        removeAuthToken()
        removeUGroup()
        removeUSCC()
        removeAvatar()
        resetRouter()
        router.push('/logout')
      })
    }
  },
  resetAuth() {
    return new Promise(resolve => {
      commit('SET_AUD', '')
      commit('SET_AUTH', '')
      commit('SET_UGROUP', '')
      commit('SET_USCC', '')
      commit('SET_AVATAR', '')
      this.clearCache()
      resolve()
    })
  },
  clearCache() {
    removeAud()
    removeAuthToken()
    removeUGroup()
    removeUSCC()
    removeAvatar()
  }
}

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters
}

