/**
 * @param {string} path
 * @returns {Boolean}
 */
export function isExternal(path) {
  return /^(https?:|mailto:|tel:)/.test(path)
}

/**
 * @param {string} str
 * @returns {Boolean}
 */
export function validUsername(s) {
  // const valid_map = ['admin', 'editor']
  // return valid_map.indexOf(str.trim()) >= 0
  return s !== null && s.trim().length > 3
}

export function validFixedLinePhone(s) {
  return s !== null && /^((0\d{2,3})-)?(\d{7,8})(-(\d{3,}))?$/.test(s)
}

export function validMobilePhone(s) {
  return s !== null && s.length === 11 && /^(((13[0-9]{1})|(15[0-9]{1})|(18[0-9]{1})|(14[0-9]{1})|)+\d{8})$/.test(s)
}
